<template>
  <div>
    <li class="altsListFooterItem borderTop">
      <span class="invBox lightened"><span class="grey hideDesk">inv: </span>{{sumInvested | numFormat('0,0')}}</span>
      <span class="plBox profitNumber" :class="{'green':sumProfit>0,'red':sumProfit<0}"><span v-if="sumProfit>0">+</span>{{sumProfit | numFormat('0,0')}}</span>
      <span class="nowBox lightened">
        <span class="arrow" :class="{'green':sumProfit>0,'red':sumProfit<0}">
        =
        </span>
      {{sumNow | numFormat('0,0')}}</span>
    </li>
    <li class="altsListFooterItem" v-if="isShowAK&&list.length===$store.state.infoMain.tradeList.length">
      <span class="invBox lightened"><span class="grey hideDesk">inv: </span>{{sumInvestedAK | numFormat('0,0')}}</span>
      <span class="plBox profitNumber" :class="{'green':sumProfitAK>0,'red':sumProfitAK<0}"><span v-if="sumProfitAK>0">+</span>{{sumProfitAK | numFormat('0,0')}}</span>
      <span class="nowBox lightened">
        <span class="arrow" :class="{'green':sumProfitAK>0,'red':sumProfitAK<0}">
        =
        </span>
      {{sumNowAK | numFormat('0,0')}}</span>
    </li>
    <li class="altsListFooterItem" v-if="!isShowAK && !$route.name==='hyp'">
      <span class="invBox grey ml5">{{sumNow/totalNow | numFormat('% 0,0.0')}}</span>
      <span class="plBox"> </span>
      <span class="nowBox grey ml5">{{sumInvested/totalInvested | numFormat('% 0,0.0')}}</span>
    </li>
  </div>
</template>

<script>
  export default {
    props:['list','isShowAK'],
    data() {
      return {
        localSort:{
          range:'all',
          letter:'all',
          exchange:'all',
          by:'A-Z'
        },
      };
    },
    computed: {
      sumInvested(){
        if(this.list.length!==0){
          const investedArr = this.list.map(item=>item.invested)
          return investedArr.reduce((a, b) => a+b)
        }
      },
      sumNow(){
        if(this.list.length!==0){
          const nowArr = this.list.map(item=>item.valueNow)
          return nowArr.reduce((a, b) => a+b)
        }
      },
      sumProfit(){
        if(this.list.length!==0){
          const profitArr = this.list.map(item=>item.profit)
          return profitArr.reduce((a, b) => a+b)
        }
      },
      totalInvested(){
        if(this.$store.state.infoMain.tradeList.length!==0){
          return this.$store.state.infoMain.tradeList.map(item=>item.totalAmount*item.averagePrice).reduce((a, b) => a+b);
        }else return 0
      },
      totalNow(){
        if(this.$store.state.infoMain.tradeList.length!==0){
          return this.$store.state.infoMain.tradeList.map(item=> (item.totalAmount)*item.currentPrice).reduce((a, b) => a+b)
        }else return 0
      },
      sumInvestedAK(){
        if(this.$store.state.infoAK.tradeList.length!==0){
          const investedArr = this.$store.state.infoAK.tradeList.map(item=>item.invested)
          return investedArr.reduce((a, b) => a+b)
        }
      },
      sumNowAK(){
        if(this.$store.state.infoAK.tradeList.length!==0){
          const nowArr = this.$store.state.infoAK.tradeList.map(item=>item.valueNow)
          return nowArr.reduce((a, b) => a+b)
        }
      },
      sumProfitAK(){
        if(this.$store.state.infoAK.tradeList.length!==0){
          const profitArr = this.$store.state.infoAK.tradeList.map(item=>item.profit)
          return profitArr.reduce((a, b) => a+b)
        }
      },
      totalInvestedAK(){
        if(this.$store.state.infoAK.tradeList.length!==0){
          return this.$store.state.infoAK.tradeList.map(item=>item.totalAmount*item.averagePrice).reduce((a, b) => a+b);
        }else return 0
      },
      totalNowAK(){
        if(this.$store.state.infoAK.tradeList.length!==0){
          return this.$store.state.infoAK.tradeList.map(item=> (item.totalAmount)*item.currentPrice).reduce((a, b) => a+b)
        }else return 0
      },
    },
  };
</script>

<style scoped>
  .hideDesk{
    display: none;
  }
  .profitNumber{
    /*padding-left: 525px;*/
  }
  .altsListFooterItem {
