  }
  .invBox{
    width:65px;
    margin-right: 0;
  }
  .nowBox{
    width:85px;
  }
  .plBox{
    width:65px;
    margin-right: 0;
  }
  .borderTop{
    border-top:1px solid rgba(255, 255, 255, 0.4);
  }
  @media only screen and (max-width: 768px) {
