<template>
  <ul class="tradeList">
    <li class="tradeListItem pr" v-for="tradeItem in computedList" :key="tradeItem.id">
      <div class="itemItem amBox" :class="{'lightGreen':tradeItem.amount>0,'lightRed':tradeItem.amount<0}">
        <span class="label hoverLight" @click="sortBy('amount')">
        amount:
        </span>
        <span v-if="tradeItem.amount<=1000"> {{tradeItem.amount | numFormat('0,0.00')}}</span>
        <span v-if="tradeItem.amount>1000"> {{tradeItem.amount | numFormat('0,0')}}</span>
      </div>
      <div class="itemItem prBox">
        <span class="label hoverLight" @click="sortBy('price')">
        price:
        </span>
        <span v-if="tradeItem.price<=1000"> {{modifyNumber(tradeItem.price)}}</span>
        <span v-if="tradeItem.price>1000"> {{tradeItem.price | numFormat('$ 0,0')}}</span>
        <span
          @click="setAlarm(tradeItem.amount,tradeItem.price,item.currentPrice)"
          class="hand"
          :class="{'handGreen':tradeItem.price<item.currentPrice&&tradeItem.amount>0,'handRed':tradeItem.price>item.currentPrice&&tradeItem.amount>0}"
          v-if="item.coin!=='usdt'"
        />
      </div>
      <div class="itemItem totalBox" :class="{'lightGreen':tradeItem.amount>0,'lightRed':tradeItem.amount<0}">
        ({{tradeItem.price*tradeItem.amount | numFormat('$ 0,0.00')}})
      </div>
      <!-- item.sellPrice изначально нет, но с v-model он создается-->
      <input
        v-if="tradeItem.amount>0 && item.coin!=='usdt'"
        class="itemItem inputBox"
        placeholder="sell price + Etr"
        type="text"
        v-model="tradeItem.sellPrice"
        v-on:keyup.enter="sellBackTrade(tradeItem.id,tradeItem.sellPrice,tradeItem.comment)"
        @focus="(()=>{setClosingtradeItemId(tradeItem.id); tradeItem.sellPrice=item.currentPrice})"
        @focusout="()=>{tradeItem.sellPrice=''}"
      />
      <div class="itemItem diffBoxNone" v-if="tradeItem.amount<=0 && item.coin!=='usdt'"/>
      <div class="itemItem diffBox" v-if="item.coin!=='usdt'">
        <span v-if="tradeItem.id===inputId && tradeItem.amount>0">
          <span class="label">diff: </span>
          {{(tradeItem.sellPrice * tradeItem.amount)-(tradeItem.price * tradeItem.amount) | numFormat('0,0.00')}}
        </span>
      </div>
      <div class="itemItem dateBox grey"><span class="label">date:</span> {{tradeItem.date}}</div>
      <button @click="deleteTrade(tradeItem.id)" class="delBox">x</button>
<!--      <div class="itemItem commentBox"><span class="label">comment: </span> {{tradeItem.comment}}</div>-->
      <input type="text" v-model="tradeItem.comment" class="commentInputBox" v-on:keyup.enter="(ev) => ev.target.blur()">
    </li>
    <TradeForm :inside="true" :coin="this.item.coin" :exchange="this.item.exchange" :item="this.item"/>
  </ul>
</template>

<script>
import TradeForm from './TradeForm';
import generateRandomId from '../helpers/generateRandomId';
import numberReadableFormat from '../helpers/numberReadableFormat';
  export default {
    props: ['item'],
    components: {TradeForm},
    data() {
      return {
        localStateSell:'',
        inputId:'',
        sortedBy:null
      }
    },
    computed:{
      computedList(){
        if(!this.sortedBy){return this.item.trades}//без сортировки
        const by = this.sortedBy;
        return this.item.trades.sort(function (a, b) {//с сортировкой при нажатии на лейблы
          if (a[by] > b[by]) {
            return 1;
          }
          if (a[by] < b[by]) {
            return -1;
          }
          return 0;
        });
      }
    },
    methods:{
      modifyNumber(number){
        return numberReadableFormat(number)
      },
      sortBy(by){
        this.sortedBy = by
      },
      setClosingtradeItemId(id){
        this.inputId = id;
      },
      deleteTrade(id){
        this.$store.commit('deleteTrade', {coin:this.item.coin, id, pageName:this.$route.name})
        if(this.$route.name==='main'){
          this.$store.commit('calculate',{coin:this.item.coin});
        }
      },
      sellBackTrade(id,sellPrice,comment){
        this.$store.commit('sellBackTrade', {coin:this.item.coin, id, sellPrice, comment:comment='sellBack'})
        this.localStateSell='';
      },
      setAlarm(amount,price,curr){
        if(price>curr && amount>0){
          const item = {
            id:generateRandomId(),
            coin:this.item.coin.toLowerCase(),
            date:new Date().toLocaleString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' }),
            desiredPrice: price,
            comment: 'sell back bad trade',
            amount:-1*amount,//превращаем в минусовую цифру = продажа
          }
          this.$store.commit('addAlarm', {item,info:this.$store.state.isAK?'infoAK':'infoMain',listName:'alarmAltsList'});
        }
      }
    }
  };
</script>

