<template>
  <ul class="card p20 pr" sticky-container style="margin-bottom: 50px;">
    <div class="amountCoins hover">
      <span class="ml20">{{computedSortedNewList.length}} шт</span>
    </div>
    <button
      class="plusAKButton hover"
      type="button"
      @click="isShowAK=!isShowAK"
      :class="{'lightened':isShowAK}"
      v-if="!$store.state.isAK"
    >
      {{isShowAK?'-':'+'}} {{$store.state.userId[0]!=='4'?'A':'K'}}
    </button>
    <div class="plEqualExchange" >
      <FilterBlock items-name="riskCategories" :selected-item.sync="selectedRiskCategory"/>
      <FilterBlock items-name="industryCategories" :selected-item.sync="selectedIndustryCategory"/>
      <FilterBlock items-name="letters" :selected-item.sync="selectedLetter"/>
      <FilterBlock items-name="exchanges" :list="list" :selected-item.sync="selectedExchange"/>
      <FilterBlockLabels :selected-label.sync="selectedLabel"/>
    </div>
    <transition-group name="flip-list">
      <AltsListItem
        v-for="item in computedSortedNewList"
        :item="item"
        :key="item.id"
        :is-show-a-k="isShowAK"
      />
    </transition-group>
    <AltsListFooter :list="computedSortedNewList" v-if="$route.name!=='trash'" :is-show-a-k="isShowAK"/>
    <TradeForm v-if="selectedRiskCategory==='all'"/>
  </ul>
</template>

<script>
  import TradeForm from '../TradeForm';
  import TradeList from '../TradeList';
  import AltsListItem from './AltsListItem';
  import AltsListHeader from './AltsListHeader';
  import AltsListFooter from './AltsListFooter';
  import FilterBlock from './FilterBlock';
  import FilterBlockLabels from './FilterBlockLabels';
  export default {
    props:['list'],
    components: {
      FilterBlockLabels,
      FilterBlock,
      AltsListHeader,
      AltsListFooter,
      AltsListItem,
      TradeList,
      TradeForm
    },
    data() {
      return {
        selectedLetter:'all',
        selectedExchange:'all',
        selectedLabel:'valueNow',
        selectedRiskCategory:'all',
        selectedIndustryCategory:'all',
        isShowAK:false
      };
    },
    methods: {
    },
    computed: {
      computedSortedNewList(){
        if(!this.list){return []}

        let newList = this.list;

        //сначала сортировка по лейблам
        const label = this.selectedLabel// сразу this.selectedLabel не получается поэтому const
        newList = newList.sort(function (a, b) {
          if (a[label] > b[label]) {
            return label==='coin'||label==='marketCapRank'?1:-1;//если алфавит то с по возрастанию, остальные по увыванию
          }
          if (a[label] < b[label]) {
            return label==='coin'||label==='marketCapRank'?-1:1;
          }
          // a должно быть равным b
          return 0;
        });

        //потом фильтрация по риск категории

        if (this.selectedRiskCategory!=='all'){
          newList = newList.filter(item => item.riskCategory === this.selectedRiskCategory)
        }
        // if (this.selectedRiskCategory==='all w/o trash'){
        //   newList = newList.filter(item => item.riskCategory !== 'trash')
        // }
        //потом фильтрация по инд категории

        if (this.selectedIndustryCategory!=='all'){
          newList = newList.filter(item => item.industryCategory === this.selectedIndustryCategory)
        }

        //потом фильтрация по алфавиту

        if (this.selectedLetter!=='all'){
          newList = newList.filter(item=>item.coin[0]===this.selectedLetter.toLowerCase());
        }

        //потом фильтрация по биржам

        if (this.selectedExchange!=='all'){
          newList = newList.filter(item => item.exchange===this.selectedExchange)
        }

        return newList
      },
    },
  };
</script>

<style scoped>
  .plEqualExchange{
    padding-left: 110px;
  }
  .bgAK{
    background-color:rgba(0, 0, 0, 1)
  }
  .amountCoins{
    position: absolute;
    right:30px;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.4);
  }
  .plusAKButton{
    position: absolute;
    left:30px;
    top:145px;
    font-size: 16px;
    font-family: "Yu Gothic UI Semibold";
    background-color: transparent;
    border:none;
    color:rgba(255, 255, 255, 0.4);
  }
  .flip-list-move {
    transition: transform 0.5s;
  }
  @media only screen and (max-width: 768px) {
    .amountCoins{
      font-size: 10px;
    }
    .plEqualExchange{
      padding-left: 0;
    }
    .plusAKButton{
      left:unset;
      right:100px;
      top:15px;
    }
  }

</style>
