<template>
  <li class="altsListItem pr">
<!--    exchange-->
    <div class="exchangeBox">
      <a :href="makeUrl(item.exchange,item.coin)" target="_blank">
        <img :src="item.exchangeUrl" style='width:100%' :alt="item.exchange"/>
      </a>
      <span class="AK hoverLight" @click="showAKLocal=!showAKLocal" v-if="isAKHasSameCoin&&!isShowAK&&!$store.state.isAK" :class="{'lightened':showAKLocal}">{{$store.state.userId[0]!=='4'?'A':'K'}}</span>
      <span class="AK hoverLight" @click="showAKLocal=!showAKLocal" v-if="isAKHasSameCoin&&!isShowAK&&$store.state.isAK" :class="{'lightened':showAKLocal}">{{$store.state.userId[0]!=='4'?'K':'A'}}</span>
    </div>
<!--    symbol-->
    <div class="symbolBox hover" @click="$store.commit('markCoin', {coin:item.coin, is:item.isMarked})">
      <img :src="item.coinUrl" style='width:100%' alt="coin ico" v-if="!item.isMarked"/>
      <img src="/img/check.png" style='width:100%' alt="coin ico" v-if="item.isMarked"/>
    </div>
<!--    coin-->
    <div class="coinBox hoverLight" @click="dropdown">
      <div class="fullname hideMob">{{item.name}}</div>
      <div class="coinname">{{item.coin?item.coin.toUpperCase():''}}</div>
    </div>
<!--    amount-->
    <div class="amBox grey" v-if="item.totalAmount<1000"><span class="grey hideDesk label">am: </span>{{item.totalAmount | numFormat('0,0.00')}}</div>
    <div class="amBox grey" v-if="item.totalAmount>=1000"><span class="grey hideDesk label">am: </span>{{item.totalAmount | numFormat('0,0')}}</div>
<!--    invested-->
    <div class="invBox">{{item.invested | numFormat('0,0')}}</div>
<!--    profit-->
    <div class="plBox" :class="{'green':item.profit>0,'red':item.profit<0}">
      <span v-if="item.profit>0">+</span>
<!--      {{item.profit | numFormat('0,0')}}-->
      {{modifyNumber(item.profit)}}
    </div>
<!--    now-->
    <div class="nowBox">
      <span class="arrow" :class="{'green':item.valueNow>item.invested,'red':item.valueNow<item.invested}">
        =
      </span>
<!--      {{item.valueNow | numFormat('0,0')}}-->
      {{modifyNumber(item.valueNow)}}

    </div>
<!--    percentage 24%-->
    <div class="percBox" :class="{'green':item.priceChangePercentage24h>0,'red':item.priceChangePercentage24h<0}">
<!--      {{item.priceChangePercentage24h| numFormat('% 0,0')}}-->
<!--      {{modifyNumber(item.priceChangePercentage24h*100)}}%-->
      {{(item.priceChangePercentage24h*100).toFixed(0)}}%
    </div>
<!--    curr price-->
    <div class="currBox curr">{{modifyNumber(item.currentPrice)}}</div>
<!--    av price-->
    <div class="avBox grey">{{modifyNumber(item.averagePrice)}}</div>
<!--    xs-->
    <div class="xsBox" :class="{'green':item.xs>1,'red':item.xs<1}">
      <span class="grey hideDesk label">xs: </span>{{item.xs | numFormat('0,0.0')}}
    </div>
<!--    dump price-->
    <div>
      <span class="grey hideDesk label">dPrice: </span>
      <input
        type="text"
        v-model.number="item.dumpPrice"
        class="dumpBox"
        v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
      >
      <span class="grey label"> =</span>
    </div>
<!--    dxs-->
    <div class="xsBox grey">
      {{(item.currentPrice/(item.dumpPrice)) | numFormat('0,0.0')}}
      <span class="grey label">х</span>
    </div>
<!--    ath price-->
    <div>
      <span class="grey hideDesk label">ATHPrice: </span>
      <input
        type="text"
        v-model.number="item.athPrice"
        class="dumpBox"
        v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
      >
      <span class="grey label"> =</span>
    </div>
<!--    axs-->
<!--    <div class="xsBox grey">-->
<!--      {{(item.currentPrice/(item.athPrice)) | numFormat('0,0.00')}}-->
<!--      <span class="grey label">x</span>-->
    <div class="xsBox grey">
<!--      {{((-item.athPrice/(item.currentPrice))+1) | numFormat('% 0,0')}}-->
      {{modifyNumber((-item.athPrice/(item.currentPrice))+1)}}%
<!--      <span class="grey label">%</span>-->
    </div>
<!--    fixed profit-->
    <div class="fxdBox hideMob" :class="{'green':item.fixedProfit>0, 'red':item.fixedProfit<0, 'transparent':item.fixedProfit===0}">
      <span class="grey hideDesk label" v-if="item.fixedProfit">fp/l: </span>
<!--      {{item.fixedProfit | numFormat('$ 0,0')}}-->
      {{modifyNumber(item.fixedProfit)}}$
    </div>
<!--    MC#-->
    <div class="mcBox hoverLight hideMob" ><span class="grey hideDesk label">#: </span>
<!--      {{item.marketCapRank | numFormat('0,0')}}-->
      {{modifyNumber(item.marketCapRank)}}
    </div>
<!--    graph-->
    <div class="graphBox hideMob">
      <a :href="`https://www.coingecko.com/en/coins/${item.id}`" target="_blank">
      <trend
        :data="item.sparklineIn7d"
        :gradient="item.priceChangePercentage24h<0 && item.sparklineIn7d?['#f6465d']:['#42d69c']"
        :autoDrawDuration="700"
        :stroke-width="2"
        smooth
      />
      </a>
    </div>
<!--    comment-->
    <form @submit.prevent="update">
      <input type="text" v-model="item.comment" class="commentBox comment" v-on:keyup.enter="(ev) => ev.target.blur()">
    </form>
<!--    delete button-->
    <span class="hover" @click="deleteEmptyTradeCoin" v-if="!item.trades">delete empty coin</span>
<!--    popup tradelist-->
    <Transition name="slide-fade">
      <AltsListItemCopy
        :item="$store.state[$store.state.isAK?'infoAK':'infoMain'].tradeList.find(tradeListItem=>tradeListItem.coin===item.coin)"
        v-if="isShowAK||showAKLocal"
      />
    </Transition>
    <Transition name="slide-fade">
      <TradeList
        :item="item"
        v-if="isTradesDropped"
      />
    </Transition>
  </li>
</template>

<script>
import TradeList from '../TradeList';
import makeUrlFromExchangeName from '../../helpers/makeUrlFromExchangeName';
import AltsListItemCopy from './AltsListItemCopy';
import numberReadableFormat from '../../helpers/numberReadableFormat';
export default {
    components: {
      AltsListItemCopy,
      TradeList,
    },
    props:['item','isShowAK'],
    data() {
      return {
        isTradesDropped:false,
        showAKLocal:false
      };
    },
    methods: {
      modifyNumber(number){
        if(number){
          return numberReadableFormat(number)
          // return new Intl.NumberFormat().format(numberReadableFormat(number));//100,000,000
        }
      },
      makeUrl(ex,coin){
        return makeUrlFromExchangeName(ex,coin)
      },
      update(){
        if(this.$route.name==='hyp'){
          // this.$store.dispatch('updateFB',this.$store.state.hypotheticalListName);
        }else this.$store.dispatch('updateFB',{name:'tradeList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      dropdown(){
        this.isTradesDropped=!this.isTradesDropped
      },
      deleteEmptyTradeCoin(){
        this.$store.state.infoMain.tradeList = this.$store.state.infoMain.tradeList.filter(itemInItem=>itemInItem.coin!==this.item.coin)
        this.$store.dispatch('updateFB',{name:'tradeList',info:this.$store.state.isAK?'infoAK':'infoMain'});

      }
    },
    computed:{
      isAKHasSameCoin(){
        const isHas = this.$store.state[!this.$store.state.isAK?'infoAK':'infoMain'].tradeList.find(item=>item.coin===this.item.coin)
        return !!isHas;
      }
    }
  };
</script>

